.ExploreBanner_sec {
    width: 100%;
    height: auto;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
}


.slick-dots li button:before {
    color: #6A08B7;
    font-size: 14px;
}

.slick-dots li.slick-active button:before {
    color: #6A08B7;
}

.ExploreBanner_sec .content {
    width: 100%;
    max-width: 55%;
    height: auto;
    padding: 50px 0;
}

.ExploreBanner_sec .heading {
    font-size: 42px;
    text-align: left;
    font-weight: 700;
    color: #04F9EA;
    padding: 0;
    margin-bottom: 30px;
}

.ExploreBanner_sec .price_tag {
    width: 100%;
    height: auto;
    padding: 10px 20px;
    border-radius: 18px;
    background: linear-gradient(90deg, rgba(94, 16, 223, 1) 0%, rgba(51, 9, 121, 1) 100%);
    margin-bottom: 30px;
    position: relative;
}

.ExploreBanner_sec .price_tag .headline {
    font-size: 26px;
    text-align: left;
    font-weight: 600;
    line-height: normal;
    color: #fff;
    padding: 0;
    margin: 0;
}

.ExploreBanner_sec .price_tag .ctrbox {
    position: absolute;
    top: -27px;
    right: -25px;
    z-index: 1;
    width: 110px;
    height: 110px;
    border-radius: 50%;
}

.ExploreBanner_sec .launch_wrap {
    width: 100%;
    height: auto;
}

/* .ExploreBanner_sec .launch_wrap div {
    margin-bottom: 10px;
} */


.ExploreBanner_sec .launch_icon {
    width: 100%;
    max-width: 40px;
    min-width: 25px;
    height: auto;
    margin-right: 10px;
}

.ExploreBanner_sec .launch_icon img {
    width: 100%;
    height: 100%;
}

.ExploreBanner_sec .launch_wrap .head {
    font-size: 32px;
    font-weight: 700;
    line-height: normal;
    text-align: left;
    color: #fff;
    padding: 0;
    margin-bottom: 15px;
}

.ExploreBanner_sec .launch_wrap .list {
    width: 100%;
    height: auto;
    list-style: none;
    padding-left: 20px;
    margin: 0;
}

.ExploreBanner_sec .launch_wrap .list li {
    font-size: 20px;
    font-weight: 400;
    line-height: normal;
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    padding: 0;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
}

.ExploreBanner_sec .launch_wrap .list li span {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ExploreBanner_sec .launch_wrap .list li span img {
    width: 100%;
    height: 100%;
}

