    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.ExploreBanner_sec .banner_bottom .getnow_btn {
    text-decoration: none;
    outline: 0;
    box-shadow: none;
    border: none;
    width: 100%;
    max-width: 120px;
    height: 38px;
    border-radius: 20px;
    padding: 5px 20px;
    background: linear-gradient(90deg, rgba(94, 16, 223, 1) 0%, rgba(114, 41, 233, 1) 17%, rgba(51, 9, 121, 1) 100%);
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
