    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 30px;
}

.ExploreBanner_sec .banner_bottom .getnow_btn span {
    padding: 2px;
    border-radius: 50%;
    border: 1px solid #fcfbfb;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
    margin-right: 5px;
}

.ExploreBanner_sec .banner_bottom .getnow_btn span i {
