@media (max-width: 480px) {
  .svaecntcydiv {
    flex-wrap: wrap;
  }

  .quickContctBtn {
    width: 100%;
    margin: 10px 0px;
  }

  .btnsvsnd {
    margin: 10px 0px 0px 0px;
  }

  .quckcntntdiv .wfwfe3g3wtg3 {
    display: block;
  }

  .inptfgfrmdiv .countrycodebox {
    width: 100%;
    min-width: 60px !important;
    max-width: 90px !important;
    font-size: 10px;
    font-weight: 600;
  }

  .Messagebtn_ {
