.contactbg {
    width: 100%;
    max-width: 80%;
    background-color: #fff;
    height: 100%;
    margin: 10px 0;
    padding: 20px;
    border-radius: 10px;
}

.contctbrdr {
    border: 1px solid #67308F;
    padding: 15px 20px;
    border-radius: 10px;
    background-color: #67308f14;
}

.cntntflx {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    /* align-items: center; */
}

.imgbrdrdiv {
    width: 113px;
    height: 86px;
    border-radius: 50%;
    border: 2px solid #67308F;
}

.cntcnmdtlsmai {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
}

.jhntxtlws {
    color: #585454;
    font-size: 24px;
    line-height: 18px;
    font-weight: 600;
    position: relative;
    /* margin-left: -10px; */
    margin: 0;
}

.prdltxtflx {
    display: flex;
    align-items: flex-end;
    gap: 4%;
    margin-bottom: 5px;
    width: 100%;
}

.prdltxtflx.wrapbox {
    display: block;
    align-items: center;
}

.mrttxtt {
    color: #585454;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
}

.dtghhnjkd {
    width: 100%;
    max-width: 100%;
}

.jhntxtlws::after {
    content: "";
    position: absolute;
    top: 2px;
    right: -7px;
    width: 0px;
    border: 1px solid #585454;
    height: 100%;
}

.cmpntyxt {
    color: #4C58FC;
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
    position: relative;
    margin-bottom: 3px;
}

.cmpntyxt::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -7px;
    width: 0px;
    border: 1px solid #4C58FC;
    /* height: 10px; */
    height: 80%;
}

.prdltxtflx.wrapbox .cmpntyxt::after {
    display: none;
}

.dghttxt {
    color: #4C58FC;
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
}

.prdltxtflx.wrapbox .dghttxt {
    font-size: 14px;
    color: #420172;
}

.mbltxtop {
    color: #655C5C;
    font-size: 13px;
    line-height: 16px;
    font-weight: 400;
    position: relative;
}

.mbltxtop span {
    font-weight: bold;
    font-size: 14px;
}

