.iommdot {
    position: relative;
}

.iommdot .dropDelete {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    background-color: #fff;
    box-shadow: 0px 0px 5px 0px #838383;
    padding: 10px 15px;
    border-radius: 4px;
    overflow: hidden;
    width: auto;
}

.iommdot .dropDelete ul {
    list-style: none;
    margin: 0;
    display: flex;
    align-items: center;
}

.iommdot .dropDelete ul li {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: normal;
    text-align: left;
    color: #3b3a3a;
    font-weight: 600;
    margin-right: 15px;
}

.iommdot .dropDelete ul li:last-child {
    margin-right: 0;
}

.iommdot .dropDelete ul li .whatsapp {
    font-size: 22px;
    color: rgb(6, 109, 3);
    margin-right: 5px;
}

.iommdot .dropDelete ul li .trash {
    font-size: 16px;
    color: rgb(196, 2, 2);
    margin-right: 5px;
}

.iommdot .dropDelete ul li .drctrCntctIconOpenDel {
    width: 100%;
    max-width: 22px;
    min-width: 22px;
    height: 22px;
    border-radius: 50%;
}

