        gap: 10px;
    }

    .contctbrdr {
        padding: 15px 10px;
    }

    .jhntxtlws {
        font-size: 22px;
    }

    .cmpntyxt,
    .dghttxt {
        font-size: 16px;
    }
}

@media (max-width: 1024px) {
    .contactbg {
        width: 100%;
    }
}

@media (max-width: 991px) {
    .contactbg {
        width: 100%;
        max-width: 100%;
    }
}

@media(max-width:767px) {
    .cntntflx {
        /* flex-wrap: wrap; */
        /* justify-content: flex-start; */
        align-items: flex-start;
    }

    .cntcnmdtlsmai {
        width: 100%;
        flex-wrap: wrap;
    }

    .contctbrdr {
        position: relative;
    }

    .upbtndivmain {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .upbtndivmainlop {
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        row-gap: 10px;
        column-gap: 10px;
        padding: 10px 0px;
    }

    .upbtndiv {
        padding: 5px 10px;
    }

    .imgbgdrk {
        width: 80px;
        height: 80px;
    }

    .imgbrdrdiv {
        width: 90px;
        height: 86px;
        border-radius: 50%;
        border: 2px solid #67308F;
    }

    .dtghhnjkd {
        padding: 10px 0px;
    }

    .imgvrtdiv {
        width: 100%;
        max-width: 90px;
        min-width: 90px;
        height: 90px;
    }

    .jhntxtlws {
        margin-left: 0;
        font-size: 17px;
    }

    .prdltxtflx {
        align-items: center;
    }

    .lksflwresrt,
    .txtljklppp {
        font-size: 13px;
    }
}

