.WithdrawTop_Heading {
    width: 100%;
    height: auto;
    padding: 15px 30px;
    background-color: #fff;
    border-radius: 4px;
    margin-bottom: 10px;
}

.WithdrawTop_Heading .back_btn {
    text-decoration: none;
    outline: 0;
    box-shadow: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-right: 30px;
    font-size: 24px;
    color: #000;
}

.WithdrawTop_Heading .title {
    font-size: 24px;
    line-height: normal;
    text-align: left;
    font-weight: 600;
    color: #000;
    padding: 0;
    margin: 0;
}

.EarnedBalanceTransfer_sec {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1%;
    background-color: #fff;
    padding: 15px 20px 30px;
    border-radius: 4px;
    margin-bottom: 10px;
}

.EarnedBalanceTransfer_box {
    width: 100%;
    max-width: 32%;
    min-width: 32%;
    height: auto;
    padding: 20px;
    border-radius: 12px;
}

.EarnedBalanceTransfer_box .cardheding {
    font-size: 18px;
    font-weight: 400;
    text-align: left;
    line-height: normal;
    color: #fff;
    padding: 0;
    margin-bottom: 10px;
}

.EarnedBalanceTransfer_box .cardamount {
    font-size: 28px;
    font-weight: 600;
    text-align: left;
    line-height: normal;
    color: #fff;
    padding: 0;
    margin-bottom: 15px;
}

.EarnedBalanceTransfer_box .downarrow_btn {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: end;
}

.EarnedBalanceTransfer_box .downarrow_btn .btn {
    text-decoration: none;
    box-shadow: none;
    outline: 0;
    border: 1px solid #fff;
    background-color: transparent;
    border-radius: 50%;
    padding: 6px;
    width: 100%;
    max-width: 28px;
    min-width: 28px;
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 14px;
    color: #fff;
}


.Transactionhistory_sec {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 1%;
}

.Bankaccount_wrap {
    width: 100%;
    max-width: 49%;
    height: auto;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px;
}

.Bankaccount_wrap .head {
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
    text-align: left;
    color: #000;
    padding: 0;
    margin-bottom: 10px;
}

.Bankaccount_wrap .box {
    width: 100%;
    height: auto;
    padding: 20px;
    border: 1px solid #C6C6C6;
    border-radius: 12px;
}

.Bankaccount_wrap .box .text {
    font-size: 16px;
    text-align: left;
    font-weight: 400;
    color: #000000;
    padding: 0;
    margin-bottom: 5px;
}

.Bankaccount_wrap .botton_wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 10px 20px;
}

.Bankaccount_wrap .botton_wrap .btn {
    text-decoration: none;
    box-shadow: none;
    outline: 0;
    border: none;
    background-color: transparent;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    color: #000000;
}

.Transactionhistory {
    width: 100%;
    max-width: 49%;
    height: auto;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px;
}

.Transactionhistory .head {
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
    text-align: left;
    color: #000;
    padding: 0;
    margin-bottom: 10px;
}

.Transactionhistory .history_list {
    width: 100%;
    height: auto;
    list-style: none;
    padding: 0;
    margin: 0;
}

.Transactionhistory .history_list li {
    width: 100%;
    height: auto;
    padding: 15px;
    border: 1px solid #E7B4F9;
    background-color: transparent;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    margin-bottom: 10px;
}

.Transactionhistory .date {
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    text-align: left;
    color: #000;
    padding: 0;
    margin: 0;
}

.Transactionhistory .amount {
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    text-align: left;
    color: #000;
    padding: 0;
    margin: 0;
}

.Transactionhistory .status {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 10px;
}

.Transactionhistory .status span {
    font-size: 14px;
    font-weight: 400;
    line-height: normal;
    text-align: left;
    color: #000;
    padding: 0;
    margin: 0;
}

.Transactionhistory .status img {
    width: 16px;
    height: auto;
}

.EarnBalanceTransfer_Details {
    width: 100%;
    height: auto;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px;
    margin-bottom: 15px;
}

.EarnBalanceTransfer_Details table {
    width: 100%;
    height: auto;
}

.EarnBalanceTransfer_Details table tr {
    width: 100%;
