}

.EarnBalanceTransfer_Details table tr td {
    border: none;
}

.EarnBalanceTransfer_Details table .profile_pic {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    padding: 0;
    margin-right: 10px;
}

.EarnBalanceTransfer_Details table .name {
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    color: #000000;
    padding: 0;
    margin-bottom: 2px;
    white-space: nowrap;
}

.EarnBalanceTransfer_Details table .position {
    font-size: 12px;
    font-weight: 500;
    line-height: normal;
    color: #000000;
    padding: 0;
    margin: 0;
    white-space: nowrap;
}

.EarnBalanceTransfer_Details table .desc {
    font-size: 10px;
    font-weight: 400;
    line-height: normal;
    color: #363636;
    padding: 0;
    margin: 0;

}

.EarnBalanceTransfer_Details table .datetime {
    font-size: 12px;
    font-weight: 400;
    line-height: normal;
    color: #000000;
    padding: 0;
    margin: 0;
    white-space: nowrap;
}

.EarnBalanceTransfer_Details table .amount {
    font-size: 14px;
    font-weight: 700;
    line-height: normal;
    color: #000000;
    padding: 0;
    margin: 0;
    white-space: nowrap;
}

.EarnBalanceTransfer_Details table .status {
    width: 18px;
    height: auto;
}

.EarnBalanceTransfer_Details table .status img {
    width: 100%;
    height: 100%;
}


.AvailableTransfer_Balance {
    width: 100%;
    height: auto;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px 30px;
    margin-bottom: 15px;
}

.AvailableTransfer_Balance table {
    width: 100%;
    height: auto;
}

.AvailableTransfer_Balance table tr {
    width: 100%;
    height: auto;
}

.AvailableTransfer_Balance table tr td {
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    color: #4d4b4b;
