.rewards_card {
    width: 100%;
    height: auto;
    max-width: 420px;
    padding: 20px 20px 30px;
    border-radius: 12px;
    overflow: hidden;
    margin: 0 auto;
}

.rewards_card.silver {
    background-color: rgb(171, 171, 171);
}

.rewards_card.Gold {
    background-color: rgb(195, 146, 46);
}

.rewards_card.Plat {
    background-color: rgb(27, 72, 188);
}

.rewards_card .logoarea {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.rewards_card .logoarea img {
