    width: 100%;
    max-width: 50px;
    min-width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 10px;
}

.rewards_card .profile .pic img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.rewards_card .profile h4 {
    font-size: 17px;
    font-weight: 600;
    text-align: center;
    padding: 0;
    margin-bottom: 5px;
}

.rewards_card.silver .profile h4 {
    color: #000;
}

.rewards_card.Gold .profile h4 {
    color: #fff;
}

.rewards_card.Plat .profile h4 {
    color: #fff;
}

.rewards_card .profile p {
    font-size: 14px;
    font-weight: 400;
    text-align: center;
}

.rewards_card.silver .profile p {
    color: #706e6e;
}

.rewards_card.Gold .profile p {
    color: #d8d6d6;
}

.rewards_card.Plat .profile p {
    color: #d8d6d6;
}

.rewards_card .Qr_code {
    width: 100px;
    height: 100px;
    overflow: hidden;
}

.rewards_card .Qr_code img {
    width: 100%;
    height: 100%;
}

@media (max-width: 768px) {
    .rewards_card {
        margin: 0 auto 0px;
    }
}

@media (max-width: 480px) {
    .rewards_card .logoarea {
