/**moreReward_slider**/

.moreReward_slider {
    width: 100%;
    position: relative;
}

.moreReward_slider .slick-arrow.slick-next::before {
    display: none;
}

.moreReward_slider .slick-arrow.slick-prev::before {
    display: none;
}

.moreReward_slider .slick-dots.slick-thumb {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.moreReward_slider .slick-dots.slick-thumb li {
    width: 10px;
    height: 6px;
    margin: 0 3px;
    border-radius: 999px;
    transition: all 0.5s ease-in-out;
    will-change: width;
    background-color: #b6b3b3;
}

.moreReward_slider .slick-dots.slick-thumb li.slick-active {
    background-color: #662D91;
    width: 24px;
}

.moreReward_card {
    width: 100%;
    height: 230px;
    overflow: hidden;
    border-radius: 15px;
    position: relative;
}

.moreReward_card img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.moreReward_card .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}

.moreReward_card .head {
    font-size: 30px;
    font-weight: 600;
    line-height: normal;
    text-align: left;
    padding: 0;
    margin-bottom: 10px;
}

.moreReward_card .text {
    font-size: 12px;
    font-weight: 400;
    line-height: normal;
    text-align: left;
    padding: 0;
    margin-bottom: 15px;
}

.viewmore_btn {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: end;
}

.viewmore_btn .btn {
    text-decoration: none;
    outline: 0;
    box-shadow: none;
    background-color: transparent;
    border: 0;
    width: 100%;
    max-width: 150px;
    height: auto;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
}

.viewmore_btn .btn i {
    font-size: 16px;
}

.viewmore_btn .btn span {
    margin-left: 10px;
    border: 1px solid #000;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 991px) {
    .moreReward_card .head {
        font-size: 22px;
    }
}

@media (max-width: 767px) {
    .moreReward_card .head {
        font-size: 36px;
    }
}

@media (max-width: 576px) {
    .moreReward_card .head {
        font-size: 24px;
    }
}

@media (max-width: 480px) {
    .moreReward_card .head {
        font-size: 16px;
    }

    .moreReward_card {
        height: 160px;
    }

    .moreReward_card .text {
        margin-bottom: 5px;
    }

    .viewmore_btn .btn {
        max-width: 120px;
        font-size: 12px;
        padding: 0;
    }

    .viewmore_btn .btn span {
        width: 16px;
        height: 16px;
    }

    .viewmore_btn .btn span i {
