.colored-strike {
  position: relative;
  color: black; /* Original text color */
}
.colored-strike::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px; /* Thickness of the line */
  background-color: red; /* Color of the line */
  transform: translateY(-50%);
}
