* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  max-width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-color: rgba(237, 238, 255, 1);
}

html {
  max-width: 100%;
  overflow-x: hidden;
}

h1,
