  border-color: #c9c9c9;
  box-shadow: none;
}

.LoginBtm input {
  width: 100%;
  display: block;
  height: 40px;
  border: none;
  outline: 0;
  border: 1px solid #c9c9c9;
  background-color: transparent;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 15px;
}

.LoginBtm input::placeholder {
  color: #5c5959;
}

.LoginBtm input[type='checkbox'] {
  width: 13px;
  height: 13px;
  display: inline-block;
}

.LoginRem {
  font-size: 15px;
  margin-left: 5px;
  color: #5c5959;
}

.LoginBtn {
  width: 100%;
  height: 36px;
  /* background-color: rgb(26, 115, 232); */
  background-color: #67308F;
  color: #fff;
  border: none;
  border-radius: 6px;
  box-shadow: rgba(26, 115, 232, 0.15) 0rem 0.1875rem 0.1875rem 0rem, rgba(26, 115, 232, 0.2) 0rem 0.1875rem 0.0625rem -0.125rem, rgba(26, 115, 232, 0.15) 0rem 0.0625rem 0.3125rem 0rem;
  font-size: 15px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}

.LoginLink {
