/* loader design */

.loaderSec {
  width: 100%;
  height: 100vh;
  position: relative;
}

.loadermid {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loaderFig {
  width: 250px;
  height: auto;
}

.loaderFig img {
  width: 100%;
  height: 100%;
}

.loaderbtm {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
}

.loadersmlltxt {
  color: #000;
  font-size: 14px;
}

.loaderBigtxt {
  font-size: 22px;
  color: #000;
  font-weight: 500;
}

.loaderSec .progress {
  background-color: #c9c9c9;
  justify-content: flex-start;
  border-radius: 100px;
  align-items: center;
  position: relative;
  /* padding: 0 5px; */
  display: flex;
  height: 4px;
  width: 100%;
  margin-top: 20px;
}

.loaderSec .progress-value {
  animation: load 1s normal forwards;
  box-shadow: 0 10px 40px -10px #634fe0;
  border-radius: 100px;
  background: #634fe0;
  height: 30px;
  width: 0;
}

@keyframes load {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}


